<template>
  <div class="col-lg-3 col-md-3 reset-col side-bar hidden-cm-down" style="padding-left:5px; max-width:301px;"
       v-if="reload">
    <!--<div class="ads-one">
      <div class="card" style="height:260px;">

      </div>
    </div>-->
    <div class="ads-one" style="max-width:301px; margin-bottom: 10px;">
      <div class="card" style=" border-top: 3px solid #D81B60;">
        <div class="card-header" style="background:white; padding-left: 10px;">
          <h3 style="font-size:15px; font-family: 'Source Sans Pro',sans-serif; margin-bottom: 0;">Announcements</h3>
        </div>

        <div class="card-block reset-col" style="margin:0; padding:0;">
          <div class="row" style="border-bottom: 1px solid #ccc; padding: 13px 0 0px 10px;"
               v-for="title in announcement">
            <p>{{title.title}}</p>
          </div>
        </div>
      </div>
    </div>
    <div class="card" style="width: 301px; min-width: 301px; max-width: 301px;  border:none; box-shadow: none;">
      <adsense
        ad-client="ca-pub-7762094105008379"
        ad-slot="2586791242"
        ad-style="display:block;width:301px;height:250px"
        ad-format="auto">
      </adsense>
    </div>
    <div class="ads-one" style="max-width:301px; padding-top:10px; margin-bottom: 10px;">
      <div class="card" style=" border-top: 3px solid #03A9F4;">
        <div class="card-header" style="background:white; padding-left: 10px;">
          <h3 style="font-size:15px; font-family: 'Source Sans Pro',sans-serif; margin-bottom: 0;">Editor's Picks</h3>
        </div>

        <div class="card-block reset-col" style="margin:0; padding:0;">
          <div class="row" v-for="deal in editorChoice" style="border-bottom: 1px solid #ccc; padding: 10px 0 5px 0;">
            <div class="img-card a-row" style="padding-left:5px;">
              <div class="card-img-top list-img-editor d-inline-block pull-left"
                   :style="'background-image: url(' + getImage(deal.images) + '); padding:0; margin:0;'"
                   alt="Card image cap">
              </div>
              <div class="pull-left" style="width:80%; max-height:50px; padding-left: 5px;">
                <div class="row" style="max-height:15px; overflow: hidden;">
                  <router-link
                    :to="{ name: 'bargainDetail', params: { id: deal._id, title: generateTitle(deal.title) }, exact: true }">
                    {{truncateTitle(deal.title)}}


                  </router-link>
                </div>
                <div class="row" style="margin-top:6px;">
                  <span
                    style="color: rgb(66, 66, 66); font-size: 10px; margin-right:3px; text-decoration: line-through;">{{getListPrice(deal)}}</span>
                  <span :class="getColorTag(deal)"
                        style="font-size: 12px; font-weight: bold; text-transform: capitalize;"> {{getType(deal)}}</span>
                </div>
                <h6 class="reset-col"
                    style="font-size: 11px; height:10px; font-weight: bold; text-transform: capitalize;">
                  {{getSalePrice(deal)}}</h6>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="card"
         style="width: 301px; min-width: 301px; max-width: 301px;  box-shadow: none; background: none; border:none; margin-bottom: 10px;">
      <adsense
        ad-client="ca-pub-7762094105008379"
        ad-slot="2586791242"
        ad-style="display:inline-block;width:300px;height:600px"
        ad-format="auto">
      </adsense>
    </div>
    <div class="card" style="width: 301px; min-width: 301px; max-width: 301px; border:none; box-shadow: none;">
      <adsense
        ad-client="ca-pub-7762094105008379"
        ad-slot="2586791242"
        ad-style="display:block;width:301px;height:250px"
        ad-format="auto">
      </adsense>
    </div>
    <div class="sticky-scroll-2 reset-col" data-toggle="sticky-scroll-2">
      <div class="card" style="width: 301px; min-width: 301px; max-width: 301px; border:none; box-shadow: none; margin-top: 10px;">
        <a href="https://www.vultr.com/?ref=7145262" target="_blank"><img src="https://www.vultr.com/media/banner_3.png" width="300"
                                                          height="250"></a>
      </div>
      <div class="row text-center" style="margin-top:15px; max-width: 301px;">
        <p class="text-center" style="margin-bottom: 0px; padding-bottom: 0px;">
          <router-link class="term" :to="{query: {'modal': 'about' } }">About Us</router-link>
          <span class="divider"> · </span>
          <router-link class="term" :to="{query: {'modal': 'contact' } }">Contact Us</router-link>
          <span class="divider"> · </span>
          <router-link class="term" :to="{query: {'modal': 'privacy' } }">Privacy Policy</router-link>
          <span class="divider"> · </span>
          <router-link class="term" :to="{query: {'modal': 'tou' } }">Terms of Service</router-link>
          <span class="divider"> · </span>
          <router-link class="term" :to="{query: {'modal': 'affiliate' } }">Affiliate Disclosures</router-link>
        </p>
        <div class="a-row" style="margin-top: 8px;">
          <span class="term">Copyright © 2017 Best Bargains, Inc.</span>
        </div>
      </div>
    </div>
  </div>
</template>

<style>

  .sticky.is-stick {
    position: fixed;
  }

  .list-img-editor {
    min-height: 50px;
    max-height: 50px;
    width: 50px;
  }

  div.ads-one {
    min-width: 301px;
  }

  div.side-bar.col-lg-3, div.side-bar.col-md-3 {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 20%;
    -ms-flex: 0 0 20%;
    flex: 0 0 20%;
    max-width: 20%;
  }

  a.term {
    color: #616161;
    font-weight: 500;
  }

  span.divider {
    color: #212121;
    font-weight: bold;
    padding-left: 5px;
    padding-right: 5px;
  }

  .hidden-cm-down {
    display: none;
  }

  @media (min-width: 1066px) {
    .hidden-cm-down {
      display: block !important;
    }
  }
</style>

<script>
  import apiService from '../../util/apiService'
  import _ from 'lodash'
  import {dataBase} from  '../../firebase/vueFire'
  var announcement = dataBase.ref().child('announcements/')
  export default {
    name: 'ContentSideBar',
    data () {
      return {
        reload: true,
        editorChoice: [],
        announcement: {}
      }
    },
    watch: {
      $route () {
        if (this.$route.query.modal) {
          this.$ga.trackPage(this.$route.path + '?modal=' + this.$route.query.modal)
          this.$root.$emit('modal', this.$route.query)
        }
      }
    },
    computed: {},
    beforeDestroy () {
      $(window).off('scroll.sticky-scroll-2 resize.sticky-scroll-2')
    },
    mounted () {
      announcement.orderByChild('status').equalTo('ready').on('value', (response) => {
        this.announcement = _.map(response.val(), (num) => {
          return {title: num.title, dateAdded: num.dateAdded, description: num.description}
        })
      })
      if (this.$route.query.modal) {
        this.$ga.trackPage(this.$route.path + '?modal=' + this.$route.query.modal)
        this.$root.$emit('modal', this.$route.query)
      }
      apiService.getEditorChoices().then((value) => {
        this.editorChoice = value.body.docs.splice(value.body.docs.length - 10, 10)
      })

      // Custom
      var stickyToggle = function (sticky, stickyWrapper, scrollElement, top) {
        var bodyOffset = $('.deal_body').offset()
        top = stickyWrapper.offset().top
        var offsetWidth = $('.deal_body').outerWidth() + bodyOffset.left
        var stickyHeight = sticky.offset().left
        if (scrollElement.scrollTop() >= top) {
          stickyWrapper.height(stickyHeight)
          sticky.addClass("is-stick")
          sticky.css({'left': offsetWidth + 5 + 'px', 'top': '40px'})
        }
        else {
          sticky.removeClass("is-stick")
          stickyWrapper.height(0)
        }
      };

      // Find all data-toggle="sticky-onscroll" elements
      $('[data-toggle="sticky-scroll-2"]').each(function () {
        var sticky = $(this)
        var stickyWrapper = $('<div>').addClass('sticky-wrapper'); // insert hidden element to maintain actual top offset on page
        sticky.before(stickyWrapper)
        sticky.addClass('sticky')
        var offset = sticky.offset().top
        // Scroll & resize events
        $(window).on('scroll.sticky-scroll-2 resize.sticky-scroll-2', function () {
          stickyToggle(sticky, stickyWrapper, $(this), offset)
        })
        // On page load
        stickyToggle(sticky, stickyWrapper, $(window), offset)
      })
    },
    methods: {
      getSalePrice (deal) {
        if (deal.type === 'sale') {
          return '$' + deal.salePrice
        } else {
          return ''
        }
      },
      getListPrice (deal) {
        if (deal.type === 'sale') {
          return '$' + deal.listPrice
        } else {
          return ''
        }
      },
      getType (deal) {
        if (deal.type === 'sale') {
          return '(' + Math.round(((deal.listPrice - deal.salePrice) / deal.listPrice) * 100) + '% off)'
        } else if (deal.type === 'percentOff') {
          return deal.percentOff + '% off'
        } else {
          return deal.type
        }
      },
      getColorTag (deal) {
        if (deal.type === 'sale') {
          if (Math.round(((deal.listPrice - deal.salePrice) / deal.listPrice) * 100) >= 40) {
            return 'red-price-text'
          } else {
            return 'green-price-text'
          }
        } else if (deal.type === 'percentOff') {
          if (deal.percentOff >= 40) {
            return 'red-price-text'
          } else {
            return 'green-price-text'
          }
        } else {
          return 'green-price-text'
        }
      },
      getImage: function (image) {
        var value = 'https://files.slack.com/files-tmb/T2V9T5KHB-F3VV8THNV-28a9b2cb7f/pasted_image_at_2017_01_25_06_26_pm_480.png'
        if (image[0]) {
          value = image[0].url
        }
        return value || ''
      },
      truncateTitle (title) {
        return _.truncate(title, {length: 100})
      },
      generateTitle: function (title) {
        var title = title.replace(/[^A-Za-z0-9]/g, ' ').replace(/\s\s+/g, ' ').trim().replace(/ /g, "-");
        return title
      }
    }
  }
</script>



